.right{
  float: right;
}
.nav-link.disabled{
  color: #b6bcc2;
  
}
.navbar-dark .navbar-nav .nav-link{
  color: rgba(255, 255, 255,0.5);
  padding: 0px;
  font-weight: 600;
  text-align: right;
  
}

.nav-item{
  text-align: right;
}
.nav-link:hover .nav-link:focus{
  color: rgba(255, 255, 255, 0.788);

}
.navbar-header{
  /* background: white url("img/image4.jpeg") no-repeat scroll top left; */
  padding: 12px;
  color: white;
  
}
 .navbar{
  background-color: rgba(209, 226, 241, 0.5);
  color: white;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  float: right;
  
}
.navbar li {
  display: inline-block;
}

.navbar a{
  color: rgba(8, 1, 1, 0.762);
  text-decoration: none;
  padding: 10px 25px;
  display: inline-block;
  font-size: 18px;
  text-align: right;
}
.navbar a:hover{
  background: linear-gradient(45deg,rgba(22, 40, 202, 0.5),rgba(8, 159, 179, 0.5));
  border-radius: 30px;
  box-shadow: 0 0 5px white;
}
.main-nav a.active{
  background: linear-gradient(45deg,rgb(202, 109, 22,0.7),rgba(179, 125, 8, 0.788));
  border-radius: 50px;
  box-shadow: 0 0 5px white;
} 

.bg-black{
  background-color: #161616;
}
.background{
  /* background-image: url("./images/bg_2.jpg"); */
  background-size: cover;
  width: 100%;
  height: auto;
  min-height: 30rem;
  padding: 10rem 0;
  background-size: cover;
  background-attachment: scroll;
  background-position: center;
  
}

li a{
  cursor: pointer;
  float: right;
}
.container-nav{
  display: flex;
  flex-wrap: wrap;
  padding: 5px 5px; 
  position: absolute;
  margin: 20px;
  border-radius: 100px;
  width: 70%;
  height: 1.5cm;
  margin-top: 80px;
  margin-left: 29%;
  
}
label{
  display: inline-block;
  padding: 5px;
  text-align: left;
  cursor: default;
}
input {
  width: 400px; 
  padding: 12px 20px;
  margin: 8px 8px;
  box-sizing: border-box;
  border-radius: 4px;
  border: 2px solid;
  border-radius: 4px;
}
.input-name{
  width: 192px; 
  padding: 12px 20px;
  margin: 8px 7px;
  box-sizing: border-box;
  border-radius: 4px;
  border: 2px solid;
  border-radius: 4px;
}
.input-login{
  width: 90%; 
  padding: 12px 20px;
  margin: 8px 8px;
  box-sizing: border-box;
  border-radius: 4px;
  border: 2px solid;
  border-radius: 4px;
}

button {
  border-radius: 0;
}
button:focus {
  outline: 1px dotted;
  outline: 5px auto -webkit-focus-ring-color;
}

.btn-register{
  color: rgb(228, 226, 226);
  font-size: 20px;
  position: relative;
  padding: 10px 10px;
  margin-left: 50px;
  margin-top: 5px;
  text-transform: uppercase;
  border-radius: 4px;
  font-weight: 400;
  display: block;
  width: 80%;
  cursor: pointer;
  border: 1px solid rgba(3, 3, 3, 0.504);
  background-color:seagreen;
}
.form{   
   text-align: center; 
     width: 100%;  
    align-self: center; 
     margin-top: 50px;  
    margin: 5px 5px 5px 5px;  
    padding: 2px 2px 2px 2px;  
    } 

    .card-register{
      overflow: hidden;
      display: flex;
      width: 500px;
      height: 635px;
      background: rgba(207, 233, 236, 0.3);
      border-radius: 10px;
      box-shadow: 1px 1px 6px -2px rgba(28, 32, 32, 0.3);
      flex-direction: column;
      transition: all .3s ease;
      transform-style: preserve-3d;
      margin-left: auto;
      margin-top: 60px;
      margin-right: auto;
      margin-bottom: auto;
      position: relative;
      
    }

    .card-header{
      color: rgba(3, 2, 8, 0.8);
      font-size: 1.2rem;
      font-weight: 0;
      letter-spacing: 0.1px;
      text-transform: capitalize;
      margin: 0px;
      
    }
    .card-body-reg{
      padding: 2rem;
      height: 500px;
      background: radial-gradient(rgba(49, 98, 206, 0.4) 40%,rgba(8, 159, 179, 0.0) 100%);
    }
    .card-note {
      margin-top: 15px;
      margin-left: 150px;
      padding: 0rem;
      
    }
    .card-login{
      overflow: hidden;
      display: flex;
      width: 400px;
      height: 360px;
      background: rgba(199, 238, 243, 0.3);
      border-radius: 10px;
      box-shadow: 1px 1px 6px -2px rgb(35, 116, 131,0.3);
      flex-direction: column;
      transition: all .3s ease;
      transform-style: preserve-3d;
      margin-left: auto;
      margin-top: auto;
      margin-right: auto;
      margin-bottom: auto;
    }

    .card-body-log{
      padding: 2rem;
      height: 200px;
      background: radial-gradient(rgba(49, 98, 206, 0.4) 40%,rgba(8, 159, 179, 0.0) 100%);
    }
    
    .btn{
      color :rgb(12, 8, 7);
     
      font-size: 20px;
      position: relative;
      text-transform: uppercase;
      padding: 1px;
      border-radius: 4px;
      font-weight: 400;
      display: block;
      width: 100%;
      cursor: pointer;
      border: 1px solid rgba(3, 3, 3, 0.904);
      background-color: rgb(241, 241, 241);
    }

    .btn:hover{
      color: #333;
      text-decoration: none;
    }

    .btn:focus, .btn.focus{
      outline: 0;
      box-shadow: 0 0 0 0.2rem rgba(233,84,32,0.25);
    }

    .btn-success {
      color: rgb(241, 239, 239);
      font-size: 20px;
      position: relative;
      text-transform: uppercase;
      border-radius: 4px;
      font-weight: 400;
      display: block;
      width: 48%;
      cursor: pointer;
      border: 1px solid rgba(3, 3, 3, 0.3);
      background-color:rgb(68, 177, 115);
      margin-right: 5px;
    }
    .btn-secondary{
      color :rgb(241, 240, 239);
      font-size: 20px;
      position: relative;
      text-transform: uppercase;
      border-radius: 4px;
      font-weight: 400;
      display: block;
      width: 50%;
      cursor: pointer;
      border: 1px solid rgba(3, 3, 3, 0.3);
      background-color: rgb(224, 71, 71);
    }
    .col{
      flex-basis: 0;
      flex-grow: 1;
      max-width: 100%;
    }
    .col-e{
      padding-block: 10px;
    }

    .row {
      display: flex;
      flex-wrap: wrap;
      margin-right: 0px;
      margin-left: 0px;
      
    }
    
    .alert{
      font-size: small;
      float: left;
      margin-left: 15mm;
      position: relative;
    }
    code {
      font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
    }
    

